.stroke{
	
	width: 400px;
	
}
.loading{
	display: flex;
	
	position: absolute;
	width: 100%;
	height:100%;
	background-color: #000000a6;
	margin-top: -20px;
	justify-content:center;
	align-content: center;
	align-items: center;
	animation: fade 1s linear 0s alternate;
}

.stroke .stroke-svg .polygon,.path{
	
	stroke-width: 15;
	stroke: gray;
	stroke-dasharray: 500;
	stroke-dashoffset:500;

	 animation: anime-logo 1s infinite linear backwards;
    
}


@keyframes anime-logo {
  
 to{

	stroke-dashoffset:-500;
 }
 
 
}
@keyframes fade{
	0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

