.input-field {
	margin: 10px;
  position: relative;
  width: 300px;
  height: 44px;
  line-height: 44px;
}

.label1 {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	color: #23425c70;
	transition: 0.2s all;
	cursor: text;
	padding: 0px 15px 0px 15px;
  }
  
  .input1 {
	width: 100%;
	border: 0;
	outline: 0;
	padding: 0.5rem 0;
	border: 2px solid #23425C;
	box-shadow: none;
	color: #111;
	border-radius: 15px;
  
  }
  
  .input1:invalid {
	outline: 0;
  }
  
  .input1:focus,
  .input1:valid {
	border-color: #23425C;
  }
  
  .input1:focus ~ .label1,
  .input1:valid ~ .label1 {
	font-size: 14px;
	top: -30px;
	color: #23425C;
  }
  .form{
	  padding: 50px;
	  margin: 0px auto;
  }
  
  .svg-login{
	  width: 250px;
	  justify-content: center;
	  text-align: center;
